.Movie {
  background-color: #fff;
  margin-bottom: 70px;
  font-weight: 300;
  padding: 20px; border-radius: 5px;
  color: #abaeb9;
  box-shadow: 0 13px 27px -5px rgba(50,50,93,.25), 0 8px 16px -8px rgba(0,0,0,.3), 0 -6px 16px -6px rgba(0,0,0,.025);
}
.Movie img {
  position: relative; top: -50px; max-width: 150px;
  width: 100%; margin-right: 30px; box-shadow: 0 30px 60px -12px rgba(50,50,93,.23), 0 18px 36px -10px rgba(0,0,0,.3), 0 -12px 36px -8px rgba(0,0,0,.025);
}
.Movie .movie_title,
.Movie .movie_year {
  margin: 0; font-weight: 300;
}
.Movie .movie_title {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
}
.Movie .movie_genres {
  list-style-type: none;
  padding: 0;
  display: flex; flex-wrap: wrap;
  margin: 5px 0px;
}
.Movie .movie_genres li,
.Movie .movie_year {
  margin-right: 10px;
  font-size: 14px;
}
